@import "../../assests/sass/variables";

.navbar-container {
    @media (min-width: 768px) {
        .container {
            max-width: 100%;
        }
    }

    @media (min-width: 992px) {
        .container {
            max-width: 90% !important;
        }
    }

    @media (min-width: 1400px) {
        .container {
            max-width: 90% !important;
        }
    }

    z-index: 2;
    opacity: 0.9;
    min-height: 72px;
    display: -ms-flexbox;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #ffffff;
    color: rgba(33, 33, 33, 0.87);
    border-bottom: 1px solid rgba(33, 33, 33, 0.12);
}

.mobile-list {
    height: 100vh;
    opacity: 0.9;
    background: #ffffff;
    color: rgba(33, 33, 33, 0.87);
    border-bottom: 1px solid rgba(33, 33, 33, 0.12);

    .mobile-list-wrapper {
        margin-top: 2rem;
    }
}

.main-navbar {
    height: 72px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left-side-nav {
        .logo-wrapper img {
            width: 150px;
            height: auto;

            &:hover {
                opacity: 0.6;
            }
        }
    }

    .right-side-nav {
        .client-nav-links {
            display: flex;
            align-items: center;

            .nav-link {
                border-bottom: 5px solid transparent;
                padding: $padding-8 $padding-12;
                display: inline-block;
                font-size: $_font-size-14;
                line-height: 24px;
                text-decoration: none;
                font-weight: $font-weight-5;
                margin-right: $margin-16;
                color: $color-text-title;
                font-family: $font-secondary;
                opacity: 0.87;

                &:hover {
                    color: $color-grey;
                    border-bottom: 5px solid $color-green;
                    color: #23527c;
                    text-decoration: underline;
                    opacity: 0.7;
                }
            }

            .demo-btn {
                margin-left: 15px;
            }
        }
    }
}

.activeLinkClass {
    border-bottom: 5px solid $color-green !important;
    color: $color-text-content !important;
}