@import "../../assests/sass/variables";

.navbar-container {
    z-index: 2;
    opacity: 0.9;
    min-height: 64px;
    display: -ms-flexbox;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #ffffff;
    color: rgba(33, 33, 33, 0.87);
    border-bottom: 1px solid rgba(33, 33, 33, 0.12);
}

.mobile-list {
    height: 100vh;
    opacity: 0.9;
    background: #ffffff;
    color: rgba(33, 33, 33, 0.87);
    border-bottom: 1px solid rgba(33, 33, 33, 0.12);

    .mobile-list-wrapper {
        margin-top: 2rem;
    }
}

.ad-main-navbar {
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left-side-nav {
        display: flex;
        align-items: center;

        .logo-wrapper img {
            width: 120px;
            height: auto;

            &:hover {
                opacity: 0.8;
            }
        }

        .client-nav-links {
            display: flex;
            align-items: center;
            margin-left: 3.5rem;
            margin-top: 5px;

            .nav-link {
                padding: $padding-8 $padding-12;
                display: flex;
                align-items: center;
                font-size: $_font-size-14;
                line-height: 24px;
                text-decoration: none;
                font-weight: $font-weight-6;
                margin-right: $margin-38;

                i {
                    margin-left: $margin-16;
                }

                &:hover {
                    color: $color-grey;
                }

                &:focus {
                    color: $color-grey;
                }
            }
        }
    }

    .right-side-nav {
        width: 100px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        justify-content: space-between;
    }
}

.activeLinkClass {
    color: $color-green !important;
}

.buildings-cum-popover {
    display: flex;
    align-items: center;
    width: 87px;

    .exc-bulding-button {
        margin-right: 0px !important;
        padding-right: 2px !important;
    }

    i {
        font-size: 12px;
        margin-right: 1rem;
        margin-left: 3px;
        margin-right: $margin-28;
    }
}

// setting popver

.setting-popver {

    padding-top: $padding-12;

    .option-bar {
        display: flex;
        align-items: center;
        width: 285px;
    }


    .span-checkbox {
        margin-left: 1rem;

    }

    .span-link {
        //margin-left: rem;
    }


    .setting-option {
        font-size: 1.1rem;
        font-family: 'Roboto';
        font-weight: 400;
        line-height: 1.5rem;

        text-decoration: none;
        color: #12161B;
        cursor: pointer;
        border-radius: 0;

    }

    .account-setting-option-1 {
        font-size: $_font-size-16;
        text-decoration: none;
        color: $color-black;
        padding: 0.86rem;
        border-radius: 0;
        color: #ffffff;
        cursor: pointer;
        background-color: #00CFA1;

    }

    .account-setting-option-2 {
        font-size: $_font-size-16;
        text-decoration: none;
        color: $color-black;
        padding: 1.4rem;
        padding-bottom: .8rem;
        width: 100%;
        cursor: pointer;

        &:hover {
            background-color: #F5F5F5;
        }
    }
}