@import '../../../assests/sass/variables';
.vendor-coi-wrapper{
    width: 100%;
    margin: $margin-24;
    .header{
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: space-between;
        margin-bottom: $margin-8;
        h6{
            font-size: $_font-size-18;
            color: $color-text-title;
            font-family: $font-primary;
            margin-bottom: $margin-16; 
        }
        .button-wrapper{
            width: 256px;
            height: 36px;
        }
    }
}
.cio-form-wrapper{
    margin-top: 50px;
}

.cio-form-heading{
    font-size: 20px;
    color: $color-text-title;
    font-family: $font-primary;
    padding-bottom: 20px;
}

.cio-section-heading{
    color: $color-text-title;
    font-family: $font-primary;
    font-size: $_font-size-18;
    margin-bottom: 5px;
    margin-top: 8px;
}
.cio-add-btns{
    
    h4{
        color: #00cfa1;
        cursor: pointer;
        font-family: $font-primary;
        font-size: $_font-size-18;
        padding-top: $padding-12;
        &:hover{
            color: #979797;
        }
    }
}

.cio-info-box{
    p{
        color: $color-text-title;
        font-family: $font-primary;
        font-size: $_font-size-18;
        margin-bottom: 5px;
        margin-top: 8px;
    }
    h6{
    color: #979797;
    font-size: $_font-size-18;
    font-family: $font-primary;
    margin: 8px 0 15px 2px;
    }
    
}

.cio-checkbox-wrapper{
    display: flex;
    flex-direction: column;
    .MuiFormControlLabel-root{
        padding: 0 !important;
        padding-left: 10px !important;
    }
}

.cio-footer{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: right;
    margin-top: $margin-38;
    .btn-wrapper{
        width: 182px;
    }
}

.start-doller-sign{
    margin-top: $margin-18;
    margin-right: $margin-12;
    color: $color-text-content;
}



