@import '../../../assests/sass/variables';

.people-container {
    display: flex;
    justify-content: space-between;

    .heading {
        font-size: 18px;
        color: rgba(33, 33, 33, 0.87);
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    }

    .box-style {
        display: flex;
        justify-content: flex-end;
    }
}

.content-container {
    margin-top: 0.7rem;

    .people-avatar {
        width: 48px;
        height: 48px;
    }

    .people-text-h1 {
        color: $color-text-title;
        font-size: 1rem;
    }

    .people-text-h2 {
        color: #707070;
        font-size: 1rem;
    }

    .left-tab {
        display: flex;
        justify-content: flex-end;


        .close-icon {
            margin-top: 0.9rem;
            margin-left: 1rem;
            color: $color-green;
            cursor: pointer
        }
    }

}

.modal-heading {
    font-size: 1.25rem;
    font-family: $font-secondary;

    line-height: 1.6;
    letter-spacing: 0.0075em;
}